import Vue from 'vue'
import Vuex from 'vuex'

import _ from 'lodash';

import collectionsList from '../data/collectionsList';
import collections from '../data/collections';
import albums from '../data/albums';
import songs from '../data/songs';
import artists from '../data/artists';

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        collectionsList,
        collections,
        albums,
        songs,
        artists,
        currentPlayList: null,
        currentSong: null,
        currentMainStatus: null,
        currentSongStatus: 'pause',
        currentSongVolume: 50,
        searchResult: null,
        addSongs: []
    },
    getters: {
        renderCollectionsList(state) {
            state.collectionsList.forEach((collectionList) => {
                collectionList.collections = [];
                collectionList.collectionsID.forEach((id, index) => {
                    collectionList.collections[index] = state.collections.find((collection) => {
                        return collection.id === id;
                    })
                });
            });
            return state.collectionsList;
        }
    },
    mutations: {
        selectPlayList(state, { type, id }) {
            id = parseInt(id);

            let payload = {};
            if ( type === 'collection' ) {
                payload = collections.find((collection) => {
                    return collection.id === id
                });

                let newSongs = [];
                payload.songs.forEach((id) => {
                    const newSong = state.songs.find((song) => {
                        return song.id === id ? song : false
                    });
                    if ( newSong ) {
                        newSongs.push(newSong)
                    }
                });
                payload.newSongs = newSongs;

                payload.artist = 'spotify';
                console.log(payload)
            } else if ( type === 'album' ){
                payload = albums.find((album) => {
                    return album.id === id
                });

                payload.newSongs = state.songs.filter((song) => {
                    return song.albumID === payload.id
                });
                payload.artist = artists.find((artist) => {
                    return artist.id === payload.artistID
                });
            } else if ( type === 'artist' ){
                payload = artists.find((artist) => {
                    return artist.id === id
                });
                payload.artist = payload;

                payload.title = payload.name;

                const currentAlbums = albums.filter((album) => {
                    return album.artistID === payload.id
                });

                payload.newSongs = [];
                currentAlbums.forEach((album) => {
                    state.songs.forEach((song) => {
                        if ( song.albumID === album.id ) {
                            payload.songs.push(song);
                        }
                    });
                });
            } else {
                payload.newSongs = state.addSongs;
            }

            if ( type !== 'user' ) {
                payload.newSongs.forEach((song) => {
                    song.album = albums.find((album) => {
                        return album.id === song.albumID
                    });
                    song.artist = artists.find((artist) => {
                        return artist.id === song.album.artistID
                    });
                });
            }

            state.currentPlayList = payload;
        },
        selectSong(state, payload) {
            console.log(payload)
            state.currentSong = payload;
        },
        changeMainStatus(state, payload) {
            state.currentMainStatus = payload;
        },
        changeSongStatus(state, payload) {
            state.currentSongStatus = payload;
        },
        removeSong(state, payload) {
            state.addSongs = state.addSongs.filter((item) => {
                return item.id != payload.id
            })
        },
        addSong(state, payload) {
            const inStateSong = state.addSongs.find((item) => {
                return item.id === payload.id
            })
            console.log()
            if ( inStateSong ) {
                state.addSongs = payload
            } else {
                state.addSongs = [...state.addSongs, payload];
            }
            console.log(state.addSongs)
        },
        changeSongVolume(state, payload) {
            state.currentSongVolume = payload;
        },
        search(state, payload) {
            const searchSongs = state.songs.filter((song) => {
                return song.title.toLowerCase().includes(payload.toLowerCase())
            });

            const searchAlbums = state.albums.filter((album) => {
                return album.title.toLowerCase().includes(payload.toLowerCase())
            });

            const searchArtist = state.artists.filter((artist) => {
                return artist.name.toLowerCase().includes(payload.toLowerCase())
            });

            const searchCollections = state.collections.filter((collection) => {
                return collection.title.toLowerCase().includes(payload.toLowerCase())
            });

            searchArtist.forEach((artist) => {
                const matchAlbums = state.albums.filter((album) => {
                    const isInArray = searchAlbums.find((searchAlbum) => {
                        return searchAlbum.id === album.id ? true : false;
                    })

                    return album.artistID === artist.id && !isInArray
                });
                searchAlbums.push(...matchAlbums);

                const searchAlbumsID = _.map(searchAlbums, 'id');
                const matchSongs = state.songs.filter((song) => {
                    const isInArray = searchSongs.find((searchSong) => {
                        return searchSong.id === song.id ? true : false;
                    })

                    return searchAlbumsID.includes(song.albumID) && !isInArray
                });
                searchSongs.push(...matchSongs);
            });

            searchSongs.forEach((song) => {
                song.album = state.albums.find((album) => {
                    return album.id === song.albumID
                });
                song.artist = state.artists.find((artist) => {
                    return artist.id === song.album.artistID
                });
            });

            searchAlbums.forEach((album) => {
                album.artist = state.artists.find((artist) => {
                    return artist.id === album.artistID
                });
            });

            console.log({songs: searchSongs,
                albums: searchAlbums,
                artists: searchArtist,
                collections: searchCollections})
            
            state.searchResult = {songs: searchSongs,
                albums: searchAlbums,
                artists: searchArtist,
                collections: searchCollections};
        }
    }
});


