<template>
    <div>
        <h1>首頁</h1>
        <div class="collection" v-for="item in _getRenderCollectionsList" :key="item.id">
            <h2>{{ item.title }}</h2>
            <Album v-for="item2 in item.collections" :key="item2.id" :album="item2" type="collection" />
        </div>
        <div class="collection">
            <h2>專輯</h2>
            <Album v-for="item in _staAlbums" :key="item.id" :album="item" type="album" />
        </div>
    </div>
</template>

<script>
    import { mapGetters, mapState } from "vuex";

    import Album from './Album.vue';

    export default {
        data () {
        return {}
        },
        computed: {
            ...mapState({
                _staAlbums: state => state.albums
            }),
            ...mapGetters({
                _getRenderCollectionsList: 'renderCollectionsList'
            }),
        },
        components: {
            Album
        },
        mounted() {
        },
        methods: {
        }
    }
</script>

<style scoped>
    h1 {
        width: 100%;
        float: left;
        font-size: 60px;
        color: #fff;
        font-weight: 800;
        line-height: 80px;
    }
    .collection {
        width: 100%;
        float: left;
        margin: 20px 0;
    }
    .collection h2 {
        width: 100%;
        float: left;
        border-bottom: 1px solid #212121;
        font-size: 22px;
        color: #fff;
        font-weight: 800;
        line-height: 40px;
    }
</style>



