<template>
    <div class="controlBar" v-if="_staCurrentSong != null">
        <div class="left">
            <img class="cover" :src="`${IMAGE_DOMAIN}/images/album/${_staCurrentSong.album.coverImageURL}`" />
            <div class="info">
                <div class="title">{{_staCurrentSong.title}}</div>
                <div class="addBtn" @click="onAddBtnClick">
                    <i :class="`${renderAddBtnClass} icon`"></i>
                </div>
                <div class="artist">{{_staCurrentSong.artist.name}}</div>
            </div>
        </div>
        <div class="center">
            <div class="youtube">
                <youtube
                    :video-id="_staCurrentSong.youtubeID" 
                    ref="videoRef"
                    :player-vars="{autoplay: 1}"
                    @playing="onYoutubeStart"
                    @ended="onForewardClick"
                    />
            </div>
            <div class="controls">
                <div class="backwardBtn" @click="onBackwardClick">
                    <i class="step backward icon"></i>
                </div>
                <div class="playBtn" @click="onPlayClick">
                    <i :class="onYoutubeChangeStatus"></i>
                </div>
                <div class="forewardBtn" @click="onForewardClick">
                    <i class="step forward icon"></i>
                </div>
            </div>
            <div class="progress">
                <div class="passedTime">{{ progress }}</div>
                <div class="line" ref="songLineRef" @click="onSongLineClick">
                    <div :style="{width: `${progressInPercent * 100}%`}"></div>
                </div>
                <div class="leftTime">{{left}}</div>
            </div>
        </div>
        <div class="right">
            <div class="volumeBtn">
                <i class="volume up icon"></i>
            </div>
            <div class="line" ref="soundLineRef" @click="onSoundLineClick">
                <div :style="{width: `${_staCurrentSongVolume}%`}"></div>
            </div>
        </div>
    </div>
    <div v-else></div>
</template>

<script>
    import { IMAGE_DOMAIN } from '../config';
    
    import Vue from 'vue'
    import { mapMutations, mapGetters, mapState } from "vuex";
    import VueYoutube from 'vue-youtube'

    Vue.use(VueYoutube)

    export default {
        data () {
            return {
                IMAGE_DOMAIN,
                progress: 0,
                progressInPercent: 0,
                left: 0, 
                durationInSeconds: 0
            }
        },
        computed: {
            ...mapState({
                _staCurrentSong: state => state.currentSong,
                _staCurrentPlayList: state => state.currentPlayList,
                _staCurrentSongStatus: state => state.currentSongStatus,
                _staAddSongs: state => state.addSongs,
                _staCurrentMainStatus: state => state.currentMainStatus,
                _staCurrentSongVolume: state => state.currentSongVolume,
            }),
            ...mapGetters({
                _getRenderCollectionsList: 'renderCollectionsList'
            }),
            renderAddBtnClass() {
                const inAddSongs = this._staAddSongs.find((song) => {
                    return song.id === this._staCurrentSong.id;
                });
                if ( inAddSongs ) {
                    return "check";
                } else {
                    return "plus";
                }
            },
            onYoutubeChangeStatus() {
                if (this._staCurrentSongStatus === 'play') {
                    return 'pause icon'
                }
                return 'play icon'
            }
        },
        watch: {
            _staCurrentSongStatus: function(newValue, oldValue) {
                if ( this.$refs.videoRef ) {
                    if ( newValue === 'play' ) {
                        this.$refs.videoRef.player.playVideo();
                    } else if ( newValue === 'pause' ) {
                        this.$refs.videoRef.player.pauseVideo();
                    }
                }
            },
            _staCurrentSongVolume: function(newValue, oldValue) {
                this.$refs.videoRef.player.setVolume(newValue);
            },
        },
        mounted() {
            this.onYoutubeProgress();
        },
        methods: {
            ...mapMutations({
                _mutRemoveSong: "removeSong",
                _mutAddSong: "addSong",
                _mutSelectSong: "selectSong",
                _mutChangeSongStatus: "changeSongStatus",
                _mutChangeSongVolume: "changeSongVolume",
            }),
            onYoutubeStart() {
                const promise = Promise.resolve(this.$refs.videoRef.player.getDuration());
                promise.then((seconds) => {
                    this.durationInSeconds = seconds
                })
            },
            onYoutubeProgress() {
                setInterval(() => {
                    if ( this.$refs.videoRef ) {
                        const promise = Promise.resolve(this.$refs.videoRef.player.getCurrentTime());
                        promise.then((seconds) => {
                            this.progress = this.formatTime(seconds);
                            this.progressInPercent = seconds / this.durationInSeconds;
                            this.left = this.formatTime(this.durationInSeconds - seconds);
                        });
                    }
                }, 1000)
            },
            formatTime(time) {
                const format = {};

                format.hours = Math.floor(time / 60 / 60);
                format.minute = Math.floor((time - (format.hours * 60 * 60)) / 60);
                format.second = Math.floor(time - (format.minute * 60) - (format.hours * 60 * 60) );

                return `${format.hours === 0 ? '' : format.hours + ':'}
                        ${format.minute}:
                        ${String(format.second).padStart(2, '0')}`;
            },
            onAddBtnClick() {
                const inAddSongs = this._staAddSongs.find((song) => {
                    return song.id === this._staCurrentSong.id;
                });
                if ( inAddSongs ) {
                    this._mutRemoveSong(this._staCurrentSong);
                } else {
                    this._mutAddSong(this._staCurrentSong);
                }
            },
            onBackwardClick() {
                let song = {};
                const playList = this._staCurrentMainStatus === 'playlist' ? this._staCurrentPlayList.newSongs : this._staAddSongs;
                playList.forEach((item, index, array) => {
                    if ( item.id === this._staCurrentSong.id ) {
                        if ( index - 1 < 0 ) {
                            song = playList[playList.length - 1];
                        } else {
                            song = playList[index - 1];
                        }
                    }
                });

                this._mutSelectSong(song);
            },
            onPlayClick() {
                this._staCurrentSongStatus === "play" ? this._mutChangeSongStatus('pause') : this._mutChangeSongStatus('play')
            },
            onForewardClick() {
                let song = {};
                const playList = this._staCurrentMainStatus === 'playlist' ? this._staCurrentPlayList.newSongs : this._staAddSongs;
                playList.forEach((item, index, array) => {
                    if ( item.id === this._staCurrentSong.id ) {
                        if ( index + 1 >= playList.length ) {
                            song = playList[0];
                        } else {
                            song = playList[index + 1];
                        }
                    }
                });

                this._mutSelectSong(song);
            },
            onSongLineClick(event) {
                const percent = event.offsetX / this.$refs.songLineRef.clientWidth;
                this.$refs.videoRef.player.seekTo(this.durationInSeconds * percent, 'seconds');
            },
            onSoundLineClick(event) {
                this._mutChangeSongVolume((event.offsetX / this.$refs.soundLineRef.clientWidth) * 100);
            }
        }
    }
</script>

<style scoped>
    .controlBar {
        width: 100%;
        position: fixed;
        bottom: 0;
        padding: 10px;
        background-color: #282828;
        display: flex;
        align-items: center;
    }
    .controlBar .left {
        width: 20%;
        float: left;
        display: flex;
    }
    .controlBar .left .cover {
        width: 100px;
        height: 100px;
        float: left;
    }
    .controlBar .left .info {
        float: left;
        margin-left: 10px;
    }
    .controlBar .left .info > div {
        float: left;
        margin: 5px;
        color: #fff;
        line-height: 24px;
        font-size: 16px;
    }
    .controlBar .left .info .title {
    }
    .controlBar .left .info .addBtn {
        cursor: pointer;
    }
    .controlBar .left .info .artist {
        clear: left;
        font-size: 12px;
    }
    .controlBar .center {
        width: 60%;
        float: left;
        padding: 10px;
    }
    .controlBar .center .youtube {
        width: 1px;
        height: 1px;
        opacity: 0;
        position: absolute;
        z-index: -9999;
    }
    .controlBar .center .controls {
        width: 100%;
        margin: 0 10px;
        float: left;
        display: flex;
        justify-content: center;
    }
    .controlBar .center .controls > div {
        width: 25px;
        height: 25px;
        float: left;
        margin: 0 10px;
        color: #fff;
        border-radius: 14px;
        cursor: pointer;
    }
    .controlBar .center .progress {
        width: 100%;
        margin: 10px;
        float: left;
    }
    .controlBar .center .progress > div {
        float: left;
        margin: 0 10px;
        color: #fff;
        font-size: 12px;
        line-height: 20px;
    }
    .controlBar .center .progress .passedTime {
    }
    .controlBar .center .progress .line {
        width: 80%;
        height: 5px;
        margin-top: 7px;
        background-color: #404040;
        border-radius: 5px;
        cursor: pointer;
    }
    .controlBar .center .progress .line div {
        height: 5px;
        float: left;
        background-color: #b3b3b3;
        border-radius: 5px;
    }
    .controlBar .center .progress .leftTime {
    }
    .controlBar .right {
        width: 20%;
        float: left;
    }
    .controlBar .right .volumeBtn {
        width: 25px;
        height: 25px;
        float: left;
        margin: 0 10px;
        color: #fff;
        border-radius: 14px;
        cursor: pointer;
    }
    .controlBar .right .line {
        width: 80%;
        height: 5px;
        margin-top: 7px;
        float: left;
        background-color: #404040;
        border-radius: 5px;
        cursor: pointer;
    }
    .controlBar .right .line div {
        height: 5px;
        float: left;
        background-color: #b3b3b3;
        border-radius: 5px;
    }
</style>


