<template>
    <div class="addressBar">
        <div class="address">
            <div class="back" @click="$router.go(-1)">
                <i class="angle left icon"></i>
            </div>
            <div class="forward" @click="$router.go(1)">
                <i class="angle right icon"></i>
            </div>
        </div>
        <input class="search" placeholder="搜尋" v-model="searchValue" />
    </div>
</template>

<script>

  export default {
    data () {
      return {
          searchValue: ''
      }
    },
    watch: {
        searchValue(value) {
            this.$router.push(`/search/${value}`)
        }
    },
    mounted() {
        if ( this.$route.params.value ) {
            this.searchValue = this.$route.params.value
        }
    }
  }
</script>


<style scoped>
    .addressBar {
        width: 100%;
        float: left;
        padding: 10px 0;
    }
    .addressBar .address {
        float: left;
    }
    .addressBar .address > div {
        float: left;
        margin: 0 10px;
        color: #696969;
        font-size: 20px;
        line-height: 24px;
        cursor: pointer;
    }
    .addressBar .search {
        width: 150px;
        margin: 0;
        padding: 0 10px;
        border-radius: 20px;
        background-color: #fff;
        line-height: 24px;
        font-size: 16px;
        color: #4b4b4b;
        border: none;
    }
</style>



