<template>
    <div v-if="_staCurrentPlayList != null">
        <div class="header">
            <img class="cover" :src="`${IMAGE_DOMAIN}/images/${$route.params.type}/${_staCurrentPlayList.coverImageURL}`" />
            <div class="info">
                <div class="type">
                    {{ $route.params.type === 'collection' ? '播放列表' : '專輯' }}
                </div>
                <div class="title">
                    {{ _staCurrentPlayList.title }}
                </div>
                <div class="description">
                    {{ _staCurrentPlayList.description }}
                </div>
                <div class="info">
                    建立者：<span>{{ _staCurrentPlayList.artist.name }}</span>・<span>{{_staCurrentPlayList.newSongs ? _staCurrentPlayList.newSongs.length : 0 }}</span>首歌曲, 
                    <span>{{ renderTotalDuration }}</span>
                </div>
                <Button />
            </div>
        </div>
        <div class="search">
            <i class="search icon"></i>
            <input placeholder="篩選" v-model="filterValue" />
        </div>
        <ul class="playlist">
            <li class="header">
                <div></div>
                <div></div>
                <div>標題</div>
                <div>藝人</div>
                <div>專輯</div>
                <div>曲長</div>
            </li>
            <div v-if="renderSongs.length === 0 && filterValue === ''">
                <Song v-for="item in _staCurrentPlayList.newSongs" :song="item" mainStatus="playlist" :key="item.id" />
            </div>
            <div v-else>
                <Song v-for="item in renderSongs" :song="item" mainStatus="playlist" :key="item.id" />
            </div>
        </ul>
    </div>
</template>

<script>
    import { IMAGE_DOMAIN } from '../config';
    
    import { mapState, mapMutations } from "vuex";

    import Song from './Song.vue';
    import Button from './Button.vue';

    export default {
        data () {
            return {
                IMAGE_DOMAIN,
                filterValue: ''
            }
        },
        computed: {
            ...mapState({
                _staAlbums: state => state.albums,
                _staCollections: state => state.collections,
                _staCurrentPlayList: state => state.currentPlayList,
            }),
            renderSongs() {
                if ( this.filterValue !== '' ) {
                    let newSongs = [];
                    newSongs = this._staCurrentPlayList.newSongs;
                    if ( event ) {
                        newSongs = newSongs.filter((item) => {
                            if ( item.title.toLowerCase().includes(this.filterValue.toLowerCase()) ) {
                                return item
                            }
                        });
                    }
                    return newSongs;
                } else {
                    return [];
                }
            },
            renderTotalDuration() {
                if ( this._staCurrentPlayList.newSongs ) {
                    let totalDuration = 0;
                    this._staCurrentPlayList.newSongs.map(function(item){
                        totalDuration += item.duration;
                    });
                    const format = {};

                    format.hours = Math.floor(totalDuration / 60 / 60);
                    format.minute = Math.floor((totalDuration - (format.hours * 60 * 60)) / 60);
                    format.second = Math.floor(totalDuration - (format.minute * 60) - (format.hours * 60 * 60) );
                    return `${format.hours} 時 ${String(format.minute).padStart(2, '0')} 分`;
                } else {
                    return '00:00'
                }
            }
        },
        components: {
            Song,
            Button
        },
        mounted() {
            this._mutSelectPlayList({
                type: this.$route.params.type,
                id: this.$route.params.id
            });
        },
        methods: {
            ...mapMutations({
                _mutSelectPlayList: "selectPlayList"
            }),
            filterSongs() {
                let newSongs = [];
                newSongs = this._staCurrentPlayList.songs;
                if ( event ) {
                    newSongs = newSongs.filter((item) => {
                        if ( item.title.toLowerCase().includes(this.filterValue.toLowerCase()) ) {
                            return item
                        }
                    });
                }
                this.renderSongs = newSongs;
            }
        }
    }
</script>

<style scoped>
    .header {
        width: 100%;
        float: left;
        margin: 20px 0;
        display: flex;
    }
    .header .cover {
        width: 250px;
        height: 250px;
        float: left;
    }
    .header .info {
        float: left;
        margin: 0 20px;
        font-size: 16px;
        color: #7e7e7e;
        line-height: 22px;
    }
    .header .info > div {
        margin: 10px 0;
    }
    .header .info .type {
        margin: 5px 0;
        font-size: 12px;
        color: #b2b2b2;
    }
    .header .info .title {
        font-size: 60px;
        color: #fff;
        font-weight: 800;
        line-height: 80px;
    }

    .search {
        width: 100%;
        float: left;
        margin: 10px 0;
    }
    .search i {
        width: 20px;
        float: left;
        margin-top: 4px;
        color: #515151;
    }
    .search input {
        width: 95%;
        float: left;
        border: none;
        border-radius: 10px;
        background: none;
        color: #515151;
        line-height: 24px;
        font-size: 16px;
    }

    .playlist {
        width: 100%;
        float: left;
        margin: 20px 0;
    }
    .playlist li {
        width: 100%;
        float: left;
        padding: 10px 0;
        border-top: 1px solid #212121;
        border-bottom: 1px solid #212121;
        cursor: pointer;
    }
    .playlist li > div {
        float: left;
        padding: 0 10px;
        color: #fff;
        line-height: 24px;
        font-size: 16px;
    }
    .playlist li.header > div {
        height: 20px;
    }
    .playlist li.header > div:nth-child(1) {
        width: 5%;
    }
    .playlist li > div:nth-child(2) {
        width: 5%;
    }
    .playlist li > div:nth-child(3) {
        width: 25%;
    }
    .playlist li > div:nth-child(4) {
        width: 25%;
    }
    .playlist li > div:nth-child(5) {
        width: 25%;
    }
    .playlist li > div:nth-child(6) {
        width: 15%;
    }
    .playlist li.select > div {
        color: #1cb953;
    }
    .playlist li.header > div {
        color: #858585;
        font-size: 12px;
    }
    .playlist li .playBtn {
        width: 27px;
        height: 27px;
        padding: 0;
        font-size: 18px;
        border: 1px solid #fff;
        border-radius: 20px;
        cursor: pointer;
        opacity: 0;
    }
    .playlist li:hover .playBtn {
        opacity: 1;
    }
    .playlist li.select .playBtn {
        opacity: 1;
    }
    .playlist li .playBtn i {
        position: absolute;
        opacity: 0;
    }
    .playlist li:hover .playBtn i:nth-child(2) {
        opacity: 1;
    }
    .playlist li.select .playBtn i:nth-child(1) {
        opacity: 1;
    }
    .playlist li.select:hover .playBtn i:nth-child(1) {
        opacity: 0;
    }
    .playlist li .addBtn {
        cursor: pointer;
    }
</style>



