<template>
    <router-link :to="`/playList/${renderPlayList}`">
        <div class="item">
            <img class="cover" :src="renderCoverSrc" />
            <div class="info">
                <div class="title">{{renderTitle}}</div>
                <div class="artist" v-if="type === 'song' || type === 'album'">{{data.artist.name}}</div>
            </div>
        </div>
    </router-link>
</template>

<script>
    import { IMAGE_DOMAIN } from '../config';

    import { mapState } from "vuex";
    import VueTypes from 'vue-types';

    export default {
        data () {
            return {
                IMAGE_DOMAIN
            }
        },
        props: {
            type: VueTypes.string.def('song'),
            data: VueTypes.object.def({}),
        },
        mounted() {
        },
        computed: {
            ...mapState({
                _staSearchResult: state => state.searchResult,
            }),
            renderPlayList() {
                switch (this.type) {
                    case 'song':
                        return `album/${this.data.album.id}`;
                    case 'artist':
                    case 'album':
                    case 'collection':
                        return `${this.type}/${this.data.id}`;
                }
            },
            renderCoverSrc() {
                switch (this.type) {
                    case 'song':
                        return `${IMAGE_DOMAIN}/images/album/${this.data.album.coverImageURL}`;
                    case 'artist':
                        return `${IMAGE_DOMAIN}/images/artist/${this.data.coverImageURL}`;
                    case 'album':
                        return `${IMAGE_DOMAIN}/images/album/${this.data.coverImageURL}`;
                    case 'collection':
                        return `${IMAGE_DOMAIN}/images/collection/${this.data.coverImageURL}`;
                }
            },
            renderTitle() {
                switch (this.type) {
                    case 'song':
                        return this.data.title;
                    case 'artist':
                        return this.data.name;
                    case 'album':
                        return this.data.title;
                    case 'collection':
                        return this.data.title;
                }
            }
        }
    }
</script>

<style scoped>
    .item {
        width: 100%;
        float: left;
        padding: 5px;
        cursor: pointer;
        display: flex;
    }
    .item .cover {
        width: 100px;
        height: 100px;
        float: left;
    }
    .item .info {
        margin-left: 10px;
        float: left;
    }
    .item .info .title {
        float: left;
        color: #fff;
        font-size: 18px;
        line-height: 24px;
    }
    .item .info .artist {
        margin-top: 5px;
        float: left;
        clear: left;
        color: #858585;
        font-size: 16px;
        line-height: 24px;
    }
</style>


