import Vue from 'vue'
import VueRouter from 'vue-router';
import { routes } from './routers';
import store from './store/index';

import App from './components/App.vue';

Vue.use(VueRouter);

const router = new VueRouter({
  routes: routes,
  mode: 'history',
})

new Vue({
  el: '#app',
  router: router, 
  store,
  render: h => h(App)
})


