<template>
    <li :class="renderSelectClass">
        <div class="playBtn" @click="onPlayClick">
            <i class="volume up icon"></i>
            <i :class="`${renderPlayBtnClass} icon`"></i>
        </div>
        <div class="addBtn" @click="onAddClick">
            <i :class="`${renderAddBtnClass} icon`"></i>
        </div>
        <div className="title">{{song.title}}</div>
        <div className="artist">{{song.artist.name}}</div>
        <div className="album">{{song.album.title}}</div>
        <div className="duration">{{renderDuration}}</div>
    </li>
</template>

<script>
    import { mapState, mapMutations } from "vuex";
    import VueTypes from 'vue-types';

    export default {
        data () {
            return {
            }
        },
        props: {
            song: VueTypes.object.def({}),
            mainStatus: VueTypes.string.def('playlist'),
        },
        computed: {
            ...mapState({
                _staCurrentSong: state => state.currentSong,
                _staCurrentSongStatus: state => state.currentSongStatus,
                _staCurrentMainStatus: state => state.currentMainStatus,
                _staAddSongs: state => state.addSongs,
            }),
            renderSelectClass() {
                if ( ( this._staCurrentSong != undefined ) &&
                    ( this._staCurrentSong.id === this.song.id ) ) {
                    return "select";
                } else {
                    return null;
                }
            },
            renderDuration() {
                const format = {};

                format.hours = Math.floor(this.song.duration / 60 / 60);
                format.minute = Math.floor((this.song.duration - (format.hours * 60 * 60)) / 60);
                format.second = Math.floor(this.song.duration - (format.minute * 60) - (format.hours * 60 * 60) );
                return `${format.hours === 0 ? '' : format.hours + ':'} ${format.minute} : ${String(format.second).padStart(2, '0')}`;
            },
            renderPlayBtnClass() {
                if ( this.renderSelectClass === 'select' && this._staCurrentSongStatus === 'play' ) {
                    return "pause";
                } else {
                    return "play";
                }
            },
            renderAddBtnClass() {
                const inAddSongs = this._staAddSongs.find((song) => {
                    return song.id === this.song.id;
                });
                if ( inAddSongs ) {
                    return "check";
                } else {
                    return "plus";
                }
            }
        },
        components: {
        },
        mounted() {
            // this._mutSelectSong({
            //     type: this.$route.params.type,
            //     id: this.$route.params.id
            // });
        },
        methods: {
            ...mapMutations({
                _mutSelectSong: "selectSong",
                _mutChangeMainStatus: "changeMainStatus",
                _mutChangeSongStatus: "changeSongStatus",
                _mutRemoveSong: "removeSong",
                _mutAddSong: "addSong",
            }),
            onPlayClick() {
                if ( this._staCurrentSongStatus === 'play' ) {
                    this._mutChangeSongStatus('pause');
                } else {
                    this._mutSelectSong(this.song);
                    this._mutChangeMainStatus(this.mainStatus);
                    this._mutChangeSongStatus('play');
                }
            },
            onAddClick() {
                const inAddSongs = this._staAddSongs.find((song) => {
                    return song.id === this.song.id;
                });
                if ( inAddSongs ) {
                    this._mutRemoveSong(this.song);
                } else {
                    this._mutAddSong(this.song);
                }
            }

        }
    }
</script>

<style scoped>
    li {
        width: 100%;
        float: left;
        padding: 10px 0;
        border-top: 1px solid #212121;
        border-bottom: 1px solid #212121;
        cursor: pointer;
    }
    li > div {
        float: left;
        padding: 0 10px;
        color: #fff;
        line-height: 24px;
        font-size: 16px;
    }
    li.header > div {
        height: 20px;
    }
    li.header > div:nth-child(1) {
        width: 5%;
    }
    li > div:nth-child(2) {
        width: 5%;
    }
    li > div:nth-child(3) {
        width: 25%;
    }
    li > div:nth-child(4) {
        width: 25%;
    }
    li > div:nth-child(5) {
        width: 25%;
    }
    li > div:nth-child(6) {
        width: 15%;
    }
    li.select > div {
        color: #1cb953;
    }
    li.header > div {
        color: #858585;
        font-size: 12px;
    }
    li .playBtn {
        width: 27px;
        height: 27px;
        padding: 0;
        font-size: 18px;
        border: 1px solid #fff;
        border-radius: 20px;
        cursor: pointer;
        opacity: 0;
    }
    li:hover .playBtn {
        opacity: 1;
    }
    li.select .playBtn {
        opacity: 1;
    }
    li .playBtn i {
        position: absolute;
        opacity: 0;
    }
    li:hover .playBtn i:nth-child(2) {
        opacity: 1;
    }
    li.select .playBtn i:nth-child(1) {
        opacity: 1;
    }
    li.select:hover .playBtn i:nth-child(1) {
        opacity: 0;
    }
    li .addBtn {
        cursor: pointer;
    }
</style>


