<template>
    <div class="searchWrap" v-if="_staSearchResult != null">
        <div class="searchCatalog" v-if="_staSearchResult.songs.length > 0">
            <h2>歌曲</h2>
            <SearchItem v-for="item in _staSearchResult.songs" :data="item" type="song" :key="`song-${item.id}`" />
        </div>
        <div class="searchCatalog" v-if="_staSearchResult.artists.length > 0">
            <h2>藝人</h2>
            <SearchItem v-for="item in _staSearchResult.artists" :data="item" type="artist" :key="`artist-${item.id}`" />
        </div>
        <div class="searchCatalog" v-if="_staSearchResult.albums.length > 0">
            <h2>專輯</h2>
            <SearchItem v-for="item in _staSearchResult.albums" :data="item" type="album" :key="`album-${item.id}`" />
        </div>
        <div class="searchCatalog" v-if="_staSearchResult.collections.length > 0">
            <h2>播放列表</h2>
            <SearchItem v-for="item in _staSearchResult.collections" :data="item" type="collection" :key="`collection-${item.id}`" />
        </div>
    </div>
</template>

<script>
    import { mapState, mapMutations } from "vuex";

    import SearchItem from './SearchItem.vue';
    
    export default {
        data () {
            return {
            }
        },
        computed: {
            ...mapState({
                _staSearchResult: state => state.searchResult,
            }),
        },
        components: {
            SearchItem
        },
        watch: {
            '$route.params.value'(value) {
                this._mutSearch(value);
            }
        },
        mounted() {
            this._mutSearch(this.$route.params.value);
        },
        methods: {
            ...mapMutations({
                _mutSearch: "search"
            })
        }
    }
</script>

<style scoped>
    .searchWrap {
        width: 100%;
        float: left;
        display: flex;
        flex-wrap: wrap;
    }
    .searchCatalog {
        width: 50%;
        padding: 10px;
    }
    .searchCatalog h2 {
        width: 100%;
        float: left;
        padding-bottom: 10px;
        border-bottom: 1px solid #858585;
        color: #fff;
        font-size: 22px;
        line-height: 30px;
    }
    .searchCatalog a {
        width: 50%;
        float: left;
    }
</style>



