<template>
    <div class="album">
        <router-link :to="`/playList/${type}/${album.id}`" tag="li">
            <img class="cover" :src="renderCoverSrc()" />
        </router-link>
        <div class="title">{{ album.title }}</div>
        <div class="description">{{ album.description }}</div>
    </div>
</template>

<script>
    import { IMAGE_DOMAIN } from '../config';

    import { mapMutations, mapState, mapActions } from "vuex";
    import VueTypes from 'vue-types';

    export default {
        data () {
            return {}
        },
        props: {
            album: VueTypes.object.def({}),
            type: VueTypes.string.def("album")
        },
        computed: {},
        mounted() {
        },
        methods: {
            renderCoverSrc() {
                if ( this.album.songs ) {
                    return `${IMAGE_DOMAIN}/images/collection/${this.album.coverImageURL}`
                } else {
                    return `${IMAGE_DOMAIN}/images/album/${this.album.coverImageURL}`
                }
            }
        }
    }
</script>

<style scoped>
    .album {
        width: 300px;
        float: left;
        margin: 0 10px;
    }
    .album .cover {
        width: 300px;
        height: 300px;
        float: left;
        cursor: pointer;
    }
    .album .title {
        width: 300px;
        float: left;
        margin: 5px 0;
        color: #fff;
        font-size: 18px;
        line-height: 24px;
    }
    .album .description {
        width: 300px;
        float: left;
        margin: 5px 0;
        color: #b4b4b4;
        font-size: 16px;
        line-height: 22px;
    }
</style>



