<template>
    <div class="buttons">
        <div class="play" @click="onPlayClick">
            {{ this._staCurrentSongStatus === 'play' ? '暫停' : '播放' }}
        </div>
    </div>
</template>

<script>
    import { mapState, mapMutations } from "vuex";

    export default {
    data () {
        return {
        }
    },
    computed: {
        ...mapState({
            _staCurrentSong: state => state.currentSong,
            _staCurrentPlayList: state => state.currentPlayList,
            _staCurrentSongStatus: state => state.currentSongStatus
        }),
    },
    methods: {
        ...mapMutations({
            _mutSelectSong: "selectSong",
            _mutChangeSongStatus: "changeSongStatus",
        }),
        onPlayClick() {
            if ( !this._staCurrentSong ) {
                this._mutSelectSong(this._staCurrentPlayList.newSongs[0]);

                this._mutChangeSongStatus('play');
            } else {
                const isInPlayList = this._staCurrentPlayList.newSongs.find((song) => {
                    return song.id === this._staCurrentSong.id
                })
                if ( !isInPlayList ) {
                    this._mutSelectSong(this._staCurrentPlayList.newSongs[0]);
                    this._mutChangeSongStatus('play');
                } else {
                    if ( this._staCurrentSongStatus === 'play' ) {
                        this._mutChangeSongStatus('pause');
                    } else {
                        this._mutChangeSongStatus('play');
                    }
                }
            }
        }
    }
  }
</script>

<style scoped>
    .buttons {
        float: left;
        clear: left;
    }
    .buttons div {
        float: left;
        margin-right: 10px;
        border: 1px solid #fff;
        border-radius: 20px;
        line-height: 30px;
        color: #fff;
        font-size: 12px;
        text-align: center;
        cursor: pointer;
    }
    .buttons .play {
        width: 100px;
        border: none;
        background-color: #1cb953;
    }
    .buttons .follow {
        width: 200px;
    }
    .buttons .more {
        width: 30px;
    }
</style>


